.transaction-complete-page-container {
  text-align: center;
  color: #303030;

  .transaction-successful-wrap {
    padding: 10vh 0;
    h1 {
      font-size: 24px;
    }
  }
}

.transaction-id-card {
  border-radius: 20px;
  padding: 1rem;
  margin: 0 2rem;
  box-shadow: 0px 1px 10px 0px #00000029;

  .transaction-id {
    margin: 0;
    color: #00bbd4;
    font-weight: bold;
  }
}

.transaction-cta-wrap {
  width: 100%;
  position: fixed;
  display: flex;
  bottom: 0;
  left: 0;
  justify-content: center;
  align-items: center;
  padding: 1.3rem 0;

  .transaction-cta {
    font-size: 20px;
    color: #ffc107;
    text-transform: none;
  }
}

@media only screen and (min-width: 1024px) {
  .transaction-complete-page-container {
    width: 50%;
    margin: 0 auto;
  }
}
